// nh ngha cc kiu d liu cho ng dng

// Ngi tham gia chi cu
export interface Player {
  id: string;
  name: string;
}

// Loi chi ph
export enum ExpenseType {
  COURT_FEE = 'court_fee', // Tin sn
  SHUTTLE = 'shuttle',     // Tin cu
  DRINK = 'drink',         // Tin nc
  EQUIPMENT = 'equipment', // Tin qun cn, ph kin
  OTHER = 'other',         // Chi ph khc
}

// Chi ph n l
export interface Expense {
  id: string;
  type: ExpenseType;
  amount: number;
  description: string;
  divideAmong?: string[]; // ID ca cc ngi chi cn chia u chi ph ny. Nu khng c, mc nh chia cho tt c
}

// Bui nh cu
export interface Session {
  id: string;
  date: string; // ISO string
  endTime?: string; // Thi gian kt thc (ISO string)
  duration?: number; // Thi lng tnh bng pht
  courtFeePerHour?: number; // Gi thu sn mi gi
  players: Player[];
  expenses: Expense[];
  location: string;
  notes?: string;
  paymentStatus?: Record<string, boolean>; // Trng thi thanh ton theo player ID
}

// Kt qu tnh chi ph
export interface ExpenseCalculation {
  totalAmount: number;
  splitByPlayer: Record<string, number>; // Map player ID -> s tin cn tr
}
