import React from "react";
import { TouchableOpacity, Text, StyleSheet, Share, View } from "react-native";
import Ionicons from "@expo/vector-icons/Ionicons";
import { Session } from "../models/types";
import { formatCurrency } from "../utils/expenseUtils";
import { format } from "date-fns";

type ShareButtonProps = {
  session: Session;
  calculatedExpenses: {
    totalAmount: number;
    splitByPlayer: Record<string, number>;
  };
};

const ShareButton = ({ session, calculatedExpenses }: ShareButtonProps) => {
  const handleShare = async () => {
    try {
      // Chun b ni dung tin nhn
      const date = format(new Date(session.date), "dd/MM/yyyy");
      const time = format(new Date(session.date), "HH:mm");

      let message = ` BO CO CHI PH CU LNG\n\n`;
      message += ` Ngy: ${date}\n`;
      message += ` Gi: ${time}\n`;
      message += ` a im: ${session.location}\n`;
      message += ` S ngi tham gia: ${session.players.length} ngi\n`;

      if (session.notes) {
        message += ` Ghi ch: ${session.notes}\n`;
      }

      message += `\n----------------------\n\n`;

      // Thm danh sch chi tit chi ph
      message += ` CHI TIT CC KHON CHI:\n\n`;
      session.expenses.forEach((expense, index) => {
        const expenseType = getExpenseTypeLabel(expense.type);
        message += `${index + 1}. ${expenseType}: ${formatCurrency(
          expense.amount
        )}`;
        if (expense.description) {
          message += ` (${expense.description})`;
        }

        // Thm thng tin v ngi chia s chi ph ny
        if (
          expense.divideAmong &&
          expense.divideAmong.length > 0 &&
          expense.divideAmong.length < session.players.length
        ) {
          const playerNames = expense.divideAmong
            .map((id) => session.players.find((p) => p.id === id)?.name || "")
            .filter((name) => name)
            .join(", ");
          message += `\n    Chia cho: ${playerNames}`;
        }

        message += "\n\n";
      });

      message += `----------------------\n\n`;
      message += ` TNG CHI PH: ${formatCurrency(
        calculatedExpenses.totalAmount
      )}\n\n`;

      // Thm chi ph mi ngi cn tr
      message += ` CHI PH MI NGI:\n\n`;

      // Sp xp theo tn ngi chi
      const sortedPlayers = [...session.players].sort((a, b) =>
        a.name.localeCompare(b.name)
      );

      sortedPlayers.forEach((player) => {
        const amount = calculatedExpenses.splitByPlayer[player.id] || 0;
        message += ` ${player.name}: ${formatCurrency(amount)}\n`;
      });

      // Chia s tin nhn
      await Share.share({
        message,
        title: `Chi ph nh cu lng ngy ${date}`,
      });
    } catch (error) {
      console.error("Li khi chia s:", error);
    }
  };

  // Hm ly tn loi chi ph
  const getExpenseTypeLabel = (type: string): string => {
    switch (type) {
      case "court_fee":
        return "Tin sn";
      case "shuttle":
        return "Tin cu";
      case "drink":
        return "Tin nc";
      case "equipment":
        return "Tin ph kin";
      case "other":
        return "Chi ph khc";
      default:
        return "Chi ph";
    }
  };

  return (
    <TouchableOpacity style={styles.shareButton} onPress={handleShare}>
      <Ionicons name="share-social-outline" size={20} color="white" />
      <Text style={styles.shareText}>Chia s chi ph</Text>
    </TouchableOpacity>
  );
};

const styles = StyleSheet.create({
  shareButton: {
    backgroundColor: "#2196f3",
    flexDirection: "row",
    alignItems: "center",
    justifyContent: "center",
    paddingVertical: 12,
    paddingHorizontal: 16,
    borderRadius: 8,
    marginVertical: 8,
  },
  shareText: {
    color: "white",
    fontWeight: "bold",
    fontSize: 16,
    marginLeft: 8,
  },
});

export default ShareButton;
