import AsyncStorage from '@react-native-async-storage/async-storage';
import { Session, Player } from '../models/types';
import { generateId } from '../utils/expenseUtils';

// Keys for AsyncStorage
const SESSIONS_KEY = 'badminton_sessions';
const PLAYERS_KEY = 'badminton_players';

/**
 * Lu danh sch bui nh cu vo b nh
 */
export const saveSessions = async (sessions: Session[]): Promise<void> => {
  try {
    await AsyncStorage.setItem(SESSIONS_KEY, JSON.stringify(sessions));
  } catch (error) {
    console.error('Li khi lu sessions:', error);
    throw error;
  }
};

/**
 * Ly danh sch bui nh cu t b nh
 */
export const getSessions = async (): Promise<Session[]> => {
  try {
    const sessions = await AsyncStorage.getItem(SESSIONS_KEY);
    return sessions ? JSON.parse(sessions) : [];
  } catch (error) {
    console.error('Li khi c sessions:', error);
    return [];
  }
};

/**
 * Thm mt bui nh cu mi
 */
export const addSession = async (session: Omit<Session, 'id'>): Promise<Session> => {
  try {
    const sessions = await getSessions();
    const newSession: Session = {
      ...session,
      id: generateId(),
    };
    
    sessions.push(newSession);
    await saveSessions(sessions);
    return newSession;
  } catch (error) {
    console.error('Li khi thm session:', error);
    throw error;
  }
};

/**
 * Cp nht mt bui nh cu
 */
export const updateSession = async (updatedSession: Session): Promise<void> => {
  try {
    const sessions = await getSessions();
    const index = sessions.findIndex(s => s.id === updatedSession.id);
    
    if (index !== -1) {
      sessions[index] = updatedSession;
      await saveSessions(sessions);
    } else {
      throw new Error('Khng tm thy bui nh cu  cp nht');
    }
  } catch (error) {
    console.error('Li khi cp nht session:', error);
    throw error;
  }
};

/**
 * Xa mt bui nh cu
 */
export const deleteSession = async (sessionId: string): Promise<void> => {
  try {
    const sessions = await getSessions();
    const updatedSessions = sessions.filter(s => s.id !== sessionId);
    await saveSessions(updatedSessions);
  } catch (error) {
    console.error('Li khi xa session:', error);
    throw error;
  }
};

/**
 * Lu danh sch ngi chi vo b nh
 */
export const savePlayers = async (players: Player[]): Promise<void> => {
  try {
    await AsyncStorage.setItem(PLAYERS_KEY, JSON.stringify(players));
  } catch (error) {
    console.error('Li khi lu players:', error);
    throw error;
  }
};

/**
 * Ly danh sch ngi chi t b nh
 */
export const getPlayers = async (): Promise<Player[]> => {
  try {
    const players = await AsyncStorage.getItem(PLAYERS_KEY);
    return players ? JSON.parse(players) : [];
  } catch (error) {
    console.error('Li khi c players:', error);
    return [];
  }
};

/**
 * Thm ngi chi mi
 */
export const addPlayer = async (name: string): Promise<Player> => {
  try {
    const players = await getPlayers();
    const newPlayer: Player = {
      id: generateId(),
      name,
    };
    
    players.push(newPlayer);
    await savePlayers(players);
    return newPlayer;
  } catch (error) {
    console.error('Li khi thm ngi chi:', error);
    throw error;
  }
};

/**
 * Xa ngi chi
 */
export const deletePlayer = async (playerId: string): Promise<void> => {
  try {
    const players = await getPlayers();
    const updatedPlayers = players.filter(p => p.id !== playerId);
    await savePlayers(updatedPlayers);
  } catch (error) {
    console.error('Li khi xa ngi chi:', error);
    throw error;
  }
};
